.toolbar{
    position: fixed;
    top:0;
    left:0;
    background: transparent linear-gradient(180deg, #4303D6 0%, #5E03AB 100%) 0% 0% no-repeat padding-box;
    border-radius: 0px 0px 70px 0px;
   
}


.toolbar_navigation{
    padding-top: 40px;

}
@media (min-width: 800px) {
    .toolbar {
        width:102px;
        height:569px;
        border-radius: 0px px 70px 0px;
        padding-bottom: 40px;
    }
    .toolbar_navigation_items a{
 
        align-self: center;
        
    }
    
}
.toolbar_navigation_items a{
    margin-top: auto;
    margin-bottom: 2.5rem;
    width:20.6px;
    height:10.29px;
    padding-top: 20.38px;
    padding-bottom: 20px;
    padding-left: 0.1px;
    width: 100%;
}

.toolbar_navigation_items a:hover,
.toolbar_navigation_items a:active{

    color:yellow
}

.Logout{
    position: fixed;
    left:20px;
    top:584px;
}

