.tab {
    background-color: white;
    top: 242px;
    left: 870px;
    width: 25%;
    height: 50px;
    letter-spacing: 0;
    opacity: 1;
    display: inline-block;
    text-decoration: underline;
    
    color: #4603D0;
  }
  .vl {
    border-left: 4px solid gray;
    height: 50px;
    display: inline-block;
    padding-right: 60px;
  }
  .tab button {
    background-color: inherit;
    float: left;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 14px 16px;
    transition: 0.3s;
    font-size: 25px;
    text-align: center;
    color: #4603D0;
    text-decoration: underline;
  }

  .tabcontent {
    display: none;
    padding: 6px 12px;
    border: 1px solid #ccc;
    border-top: none;
  }
