.container {
    display: flex;
    justify-content: space-between;
    border-top: 4px solid  #B1B8C2;
    background: 0% 0% no-repeat padding-box;
    padding-top: 15px;
    margin-top: 15px;
    padding-left: 2rem;
  }
  
  .container div {
   
    font-weight: bold;
    background: #5D03AB 0% 0% no-repeat padding-box;
    margin-top: -40px;
    width: 45px;
    height: 45px;
    line-height: 45px;
    text-align: center;
    border-radius: 50%;
  }

  #tick-mark {
    position: relative;
    display: inline-block;

}

#tick-mark::before {
    position: absolute;
    left: 8px;
    top: 50%;
    height: 26%;
    width: 3px;
    background-color: #FFFFFF;
    content: "";
    transform: translateX(10px) rotate(-45deg);
    transform-origin: left bottom;
}

#tick-mark::after {
    position: absolute;
    left: 10px;
    bottom: 20%;
    height: 3px;
    width: 65%;
    background-color: #FFFFFF;
    content: "";
    transform: translateX(10px) rotate(-45deg);
    transform-origin: left bottom;
}

