.deployment_path{
    width: 100%;
    max-width: 500px;
    padding: 15px;
    margin: auto;
    height: 100%;
  
  }
 
  .button {
    background-color:#5E03AB;
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    width: 100%;
    max-width: 300px;
    padding: 15px;
    margin: auto;
    height: 100%;
  }
  .button:disabled {
    opacity: 0.5;
  }
  .errmsg{
    color: #089000
  }

.iFrame
{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100% !important; 
}